import React from 'react';
import './Definition.css';
import './aos.css';
import AOS from 'aos';


class Definition extends React.Component {
    componentDidMount(){
        AOS.init({duration : 1000});
    }

    render(){
        return (
            <>
            <section className="s1">
                <h1 className="H" data-aos="fade-up">{contents[0].header}</h1>
                <div className="dg">
                    <div data-aos="fade-left" className="d1">
                        <p>{contents[0].description}</p>
                    </div>
                    <div className="d2">
                        <img data-aos="fade-right" src={contents[0].src}/>
                    </div>
                </div>
            </section>
            <section className="s2">
                <h1 className="H" data-aos="fade-up">{contents[0].header}</h1>
                <div className="dg">
                    <div data-aos="fade-left" className="d1">
                        <p>{contents[0].description}</p>
                    </div>
                    <div className="d2">
                        <img data-aos="fade-right" src={contents[0].src}/>
                    </div>
                </div>
            </section>
            <section className="s3">
                <h1 className="H" data-aos="fade-up">{contents[0].header}</h1>
                <div className="dg">
                    <div data-aos="fade-left" className="d1">
                        <p>{contents[0].description}</p>
                    </div>
                    <div className="d2">
                        <img data-aos="fade-right" src={contents[0].src}/>
                    </div>
                </div>
            </section>
        </>
        )
    }
}

export default Definition;

const contents = [
    {
        header : 'Czym jest terroryzm ?',
        description : 'Terroryzm - różnie umotywowane, najczęściej ideologicznie, planowane i zorganizowane działania przestępcze pojedynczych osób lub grup, w celu wymuszenia od władz państwowych i społeczeństwa określonych zachowań i świadczeń, często naruszające dobra osób postronnych; działania te są realizowane z całą bezwzględnością, za pomocą różnych środków (nacisk psychiczny, przemoc fizyczna, użycie broni i ładunków wybuchowych), w warunkach specjalnie nadanego im rozgłosu i celowo wytworzonego w społeczeństwie lęku',
        src : "https://www.przewodnik-katolicki.pl/getattachment/2a67a647-9f85-421a-9725-b2b5f51c96c8/.aspx?width=480"},
    {
        header : 'xd',
        description : '', 
        src : ""
    },
    {
        header : 'XD',
        description : '', 
        src : ""
    }

];
